import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class TodoService {
  private initialData = ['Отпроситься у мамы', 'Пообещать не пить', 'Взять денюжку на крутое пати у мамы', 'Ухуяриться в говно и нести хуйню'];
  private todo = new BehaviorSubject(this.initialData);
  private todo$ = this.todo.asObservable();
  constructor() { }
  public get(): Observable<any> {
    return this.todo$;
  }
  public set(data): any {
    this.todo.next(data);
  }
}
