import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DoneService {
  private done = new BehaviorSubject([]);
  private done$ = this.done.asObservable();
  constructor() { }
  public get(): Observable<any> {
    return this.done$;
  }
  public set(data) {
    const newDone = this.done.getValue();
    newDone.push(data);
    this.done.next(newDone);
  }
}
